import React from 'react'
import CircularProgress from '@material-ui/core/CircularProgress'
import axios from 'axios'
import Config from '../Config'
import Utils from '../Utils'
import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import TextField from '@material-ui/core/TextField'
import Button from '@material-ui/core/Button'
import Img from 'react-image'
import DatePicker from '../components/DatePicker'

const classes = {
  cardStyle: {
    maxWidth: '800px',
    marginTop: '20px',
    marginRight: 'auto',
    marginLeft: 'auto',
  }
}

class Publish extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      completed: false,
      loading: true,
      stat: {},
      inboxData: null,
    }
    if (props.data) {
      this.state.caption = props.data.caption
      this.state.url = props.data.url
      this.state.psid = props.data.psid
      this.state.scheduled_publish_time = 0
    }
    window.changeHeader('Cng c ng bi - TongHopEvent')
  }

  componentDidMount() {
    this.loadStat()
    this.loadInbox()
  }

  async loadStat() {
    const token = Utils.getLocalStorage('token')
    const res = await axios.get(`${Config.BACKEND}/publish/stat?token=${token}`)
    if (res.data.error)
      this.setState({loading: false, stat: []})
    else
      this.setState({loading: false, stat: res.data})
  }

  async loadInbox() {
    const { data } = await Utils.makeRequest(`${Config.BACKEND}/inbox/${this.state.psid}`)
    if (data && Object.keys(data).length > 0) {
      this.setState({ inboxData: data })
    }
  }
  
  async doPublish() {
    this.setState({loading: true})
    const token = Utils.getLocalStorage('token')
    const res = await axios.post(`${Config.BACKEND}/publish?token=${token}`, {
      caption: this.state.caption,
      url: this.state.url,
      scheduled_publish_time: this.state.scheduled_publish_time
    })
    if (res.data.id)
      this.setState({loading: false, completed: true})
    else
      window.showAlert({
        title: 'Khng th ng bi',
        text: 'C li xy ra'
      })
  }

  _renderComposerCard() {
    const imgStyle = {
      height: '300px',
      maxWidth: '100%',
    }
    return (
      <Card>
        <CardContent>
          <p>
            <Button
              variant='outlined'
              size='small'
              onClick={() => window.location.hash = '#/'}>Tr v</Button>
            &nbsp;&nbsp;&nbsp;
            <b>Son bi ng</b>
          </p>
          <center>
            <Img alt=''
              src={this.state.url}
              style={imgStyle}
              loader={<div style={imgStyle}><center><br/><br/><CircularProgress /></center></div>}
            />  
          </center>
          <TextField
            label="Ni dung bi ng"
            multiline
            rowsMax="20"
            value={this.state.caption}
            onChange={event => {this.setState({ caption: event.target.value });}}
            margin="normal"
            variant="outlined"
            fullWidth
          />
        </CardContent>        
      </Card>
    )
  }

  _renderPublishCard() {
    const { scheduled_publish_time } = this.state
    const isDateOK = (scheduled_publish_time - (Date.now() / 1000)) > 15*60
    const s = scheduled_publish_time * 1000

    return (
      <Card>
        <CardContent>
          <p><b>Ln lch bi ng</b></p>
          <DatePicker
            handleDateChange={(date) => this.setState({ scheduled_publish_time: Math.round(date) })}
          />
          <br/><br/>
          {isDateOK && <Button variant='outlined' onClick={() => {
            window.showAlert({
              title: 'Xc nhn ng bi',
              text: `ng bi vo lc ${Utils.getTimeStr(s)} ngy ${Utils.getDateStr(s)} ?`,
              onClickOK: this.doPublish.bind(this),
              showCancel: true
            })
          }}>ng bi</Button>}
          {!isDateOK && <p style={{color: 'red'}}>Gi ng bi phi cch hin ti t nht 15 pht</p>}
        </CardContent>
      </Card>
    )
  }

  _renderCompletedCard() {
    const { scheduled_publish_time } = this.state
    const s = scheduled_publish_time * 1000

    return (
      <div style={classes.cardStyle}>
        <Card>
          <CardContent>
            <h2>ng bi thnh cng</h2>
            <p>Bi vit  c t gi  ng vo lc {Utils.getTimeStr(s)} ngy {Utils.getDateStr(s)}</p>
            <Button
              variant='outlined'
              onClick={() => window.location.hash = '#/'}>Tr v trang ch</Button>
          </CardContent>
        </Card>
      <br/><br/>
      </div>
    )
  }

  _renderStatCard() {
    return (
      <Card>
        <CardContent>
          <p><b>Cc bi ng  ln lch</b></p>
          <ul>
            {this.state.stat.map(s => {
              return <li key={s}>{Utils.getTimeStr(s)} ngy {Utils.getDateStr(s)}</li>
            })}
          </ul>
        </CardContent>        
      </Card>
    )
  }

  _renderInboxCard() {
    const { inboxData } = this.state;
    return (
      <Card>
        <CardContent>
          {!inboxData && <center><CircularProgress /></center>}
          {inboxData && <>
            <b>Tin nhn mi nht</b> ( trn l mi, di l c)<br/>
            {inboxData.messages.data.map(msg => <p key={msg.id}>
              <b>{msg.from.name}</b>: {msg.message}
            </p>)}
          </>}
        </CardContent>        
      </Card>
    )
  }

  _renderMain() {
    return (
      <React.Fragment>
        <div style={classes.cardStyle}>
          {this._renderComposerCard()}
        </div>
        <div style={classes.cardStyle}>
          {this._renderPublishCard()}
        </div>
        <div style={classes.cardStyle}>
          {this._renderStatCard()}
        </div>
        <div style={classes.cardStyle}>
          {this._renderInboxCard()}
        </div>
        <br />
        <br />
      </React.Fragment>
    )
  }

  _renderError() {
    return (
      <center>
        <br/><br/>
        <h2>C li xy ra</h2>
        Hy vo trang ch v th li<br/>
        <br/><br/>
      </center>
    )
  }

  render() {
    const loading = <div>
      <br/><br/><br/>
      <center><CircularProgress /></center>
    </div>

    if (this.state.loading) return loading
    if (this.state.completed) return this._renderCompletedCard()

    return this.props.data ? this._renderMain() : this._renderError()
  }
}

export default Publish